.page-header__main,
.page-header__user {
  list-style: none;
  padding: 0;
  width: 100%;
}

.page-header__user {
  margin-top: -15.5px;
}

.page-header__menu-wrapper {
  display: flex;
  height: 50px;
  justify-content: flex-end;
  background-color: #c1a067;
  margin-bottom: -15px;
}

.page-header__menu--closed {
  display: none;
}

.page-header__menu-button {
  background-color: transparent;
  border: none;
  width: 70px;
  margin-right: 10px;
  cursor: pointer;
}

.page-header__menu a {
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;

  &:hover {
    color: #302a31;
  }

  &:active {
    opacity: 0.4;
  }
}

.page-header__main-item,
.page-header__user-item {
  box-sizing: border-box;
  padding-top: 15px;
  background-color: #c1a067;
  height: 50px;
  text-align: center;
  border-bottom: #ffffff 1px solid;
}

.page-header__info {
  width: 260px;
  margin-left: auto;
  margin-right: auto;
}

.page-header__info img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

.page-header__info-wrapper {
  width: 260px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-header__search {
  position: relative;
}

.page-header__search input {
  position: relative;
  width: 260px;
  height: 45px;
  margin-top: 40px;
  box-sizing: border-box;
  padding-bottom: 10px;
  border: none;
  border-bottom: #c1a067 2px solid;
  outline: none;

  &:focus {
    border-bottom: #c16774 2px solid;
  }
}

.page-header__search::before {
  position: absolute;
  content: "";
  top: 50px;
  right: 2px;
  width: 14px;
  height: 14px;
  background-image: url("../img/search.png");
  z-index: 3;
}

.page-header__search input::placeholder {
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.page-header__contacts {
  list-style: none;
  padding: 0;
  margin-top: 30px;
  margin-left: 20px;

}

.page-header__contacts-item {
  position: relative;
  margin-bottom: 25px;
}

.page-header__contacts-item a {
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  text-decoration: none;
  color: #302a31;

  &:hover {
    color: #c1a067;
  }
}

.page-header__contacts-item--callback::before {
  content: "";
  position: absolute;
  width: 9px;
  height: 11px;
  top: 5px;
  left: -20px;
  background-image: url("../img/tel.png");
}

.page-header__contacts-item--mail::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 11px;
  top: 5px;
  left: -20px;
  background-image: url("../img/mail.png");
}

.page-header__contacts-item--time::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  top: 5px;
  left: -20px;
  background-image: url("../img/time.png");
}

.page-header__cart {
  display: none;
  position: relative;
  width: 90px;
  height: 84px;
  background-color: #c1a067;
  margin-top: 17px;
  margin-left: 110px;
}

.page-header__cart::before {
  position: absolute;
  content: "";
  width: 32px;
  height: 41px;
  top: 20px;
  left: 27px;
  background-image: url("../img/cart.png");
}

.page-header__cart::after {
  position: absolute;
  content: "4";
  color: #ffffff;
  font-size: 10px;
  font-weight: 700;
  width: 10px;
  height: 10px;
  top: 10px;
  right: 5px;
}


.page-header__catalog {
  background-color: #302a31;
  width: 100%;
  margin-bottom: 30px;
}

.page-header__catalog-button {
  width: 100%;
  height: 50px;
  margin-bottom: 0;
  box-sizing: border-box;
  border: #302a31 2px solid;
  color: #302a31;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  background-color: #ffffff;
  cursor: pointer;
}

.page-header__catalog-list {
  list-style: none;
  padding: 0;
  width: 100%;
  margin: 0 auto;
}

.page-header__catalog-list--closed {
  display: none;
}

.page-header__catalog-item {
  height: 50px;
  padding-top: 15px;
  box-sizing: border-box;
  text-align: center;
  border-bottom: #f9f9f9 1px solid;
}

.page-header__catalog-item a {
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;

  &:hover {
    color: #c16774;
  }

  &:active {
    opacity: 0.4;
  }
}

@media (min-width: @tablet-width) {
  .page-header__menu-button {
    margin-right: 30px;
  }

  .page-header__info {
    width: 400px;
  }

  .page-header__info-wrapper {
    width: 400px;
  }

  .page-header__search input {
    width: 580px;
  }

  .page-header__contacts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-left: 40px;
  }

  .page-header__contacts-item {
    text-align: left;
    width: 180px;
  }
}

@media (min-width: @desktop-width) {
  .page-header__menu-wrapper {
    display: none;
  }
  .page-header__menu {
    display: flex;
    width: 1300px;
    margin-left: auto;
    margin-right: auto;
  }
  .page-header__menu a {
    font-size: 14px;
    font-weight: 400;
    color: #302a31;
    text-decoration: none;
    text-transform: initial;
    vertical-align: middle;

    &:hover {
      color: #c1a067;
    }
  }
  .page-header__main {
    display: flex;
    justify-content: space-between;
    width: 677px;
    margin-top: 10px;
    margin-right: 354px;
  }

  .page-header__user {
    display: flex;
    justify-content: space-between;
    width: 257px;
    height: 24px;
    margin-top: 10px;
  }
  .page-header__main-item,
  .page-header__user-item {
    box-sizing: border-box;
    padding: 0;
    background-color: #ffffff;
    height: 24px;
    text-align: center;
    border: none;
  }

  .page-header__user-item--selected {
    position: relative;
  }

  .page-header__user-item--selected::before {
    position: absolute;
    top: -5px;
    right: -12px;
    content: "2";
    width: 15px;
    height: 15px;
    box-sizing: border-box;
    padding-top: 2.5px;
    border-radius: 50%;
    font-size: 10px;
    font-weight: 700;
    color: #ffffff;
    background-color: #c1a067;
  }

  .page-header__info {
    display: flex;
    width: 1300px;
    margin-left: auto;
    margin-right: auto;
  }

  .page-header__info img {
    margin: 0;
    margin-right: 100px;
  }

  .page-header__info-wrapper {
    width: 801px;
  }

  .page-header__search::before {
    top: 20px;
  }

  .page-header__search input {
    width: 801px;
    margin-top: 8px;
  }

  .page-header__contacts {
    width: 801px;
    margin-left: 0;
    justify-content: space-between;
  }

  .page-header__contacts-item {
    width: 142px;
  }

  .page-header__contacts-item a {
    letter-spacing: 1px;
    text-align: center;
  }

  .page-header__contacts-item--callback::before {
    left: -25px;
  }

  .page-header__contacts-item--mail::before {
    left: -25px;
  }

  .page-header__contacts-item--time::before {
    left: -27px;
  }

  .page-header__cart {
    display: block;
  }

  .page-header__catalog {
    margin-top: -15px;
    margin-bottom: 0;
  }

  .page-header__catalog-button {
    display: none;
  }

  .page-header__catalog-list {
    width: 1360px;
    height: 60px;
    box-sizing: border-box;
    padding-left: 30px;
    display: flex;
  }

  .page-header__catalog-item:last-child {
    width: 117px;
    background-color: #c16774;
  }

  .page-header__catalog-item {
    height: 60px;
    padding: 0;
    padding-top: 18px;
    box-sizing: border-box;
    text-align: center;
    border: none;
  }

  .page-header__catalog-item:not(:last-child) {
    margin-right: 58px;
  }


  .page-header__catalog-item a {
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: #ffffff;
    text-decoration: none;
    text-transform: initial;
    vertical-align: middle;
  }

}




