.page-footer {
  color: #ffffff;
  background-color: #302a31;
  width: 100%;
  font-size: 14px;
  line-height: 26px;
  text-align: center;
}

.page-footer h3 {
  font-size: 14px;
  font-weight: 700;
  line-height: 20px;
  text-transform: uppercase;
  letter-spacing: 2.6px;
}

.page-footer a {
  color: #ffffff;
  text-decoration: none;

  &:hover {
    color: #c1a067;
  }
}

.page-footer__wrapper {
  margin-left: auto;
  margin-right: auto;
  width: 260px;
  padding-top: 20px;
}

.page-footer__main,
.page-footer__catalog {
  display: none;
  margin-bottom: 30px;
}

.page-footer__contacts {
  margin-bottom: 30px;
}

.page-footer__contacts-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 200px;
  margin-left: auto;
  margin-right: auto;
}
.page-footer__contacts-item {
  display: block;
  text-transform: uppercase;
  font-weight: 700;
  position: relative;
}

.page-footer__contacts-item--whatsapp {
  width: 26px;
  height: 26px;
}

.page-footer__contacts-item--phone {
  width: 20px;
  height: 19px;
}

.page-footer__contacts-item--callback::before {
  content: "";
  position: absolute;
  width: 9px;
  height: 11px;
  top: 7px;
  left: 40px;
  background-image: url("../img/tel.png");
}

.page-footer__contacts-item--mail::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 11px;
  top: 7px;
  left: 37px;
  background-image: url("../img/mail.png");
}

.page-footer__contacts-item--skype::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 10px;
  top: 7px;
  left: 40px;
  background-image: url("../img/skype.png");
}

.page-footer__sign-form input {
  height: 45px;
  padding-left: 2px;
  background-color: transparent;
  border: none;
  border-bottom: #c1a067 2px solid;
}

.page-footer__sign-form input::placeholder {
  font-family: inherit;
  font-size: 12px;
  letter-spacing: 1.2px;
  line-height: 22px;
  font-weight: 400;
  color: #ffffff;
  text-transform: uppercase;
}

.page-footer__sign-form button {
  width: 110px;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: 1.2px;
  font-weight: 700;
  color: #c1a067;
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  margin-top: 20px;
}

.page-footer__join {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-footer__join a {
  display: block;
  width: 48px;
  height: 48px;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 50%;
  background-color: #ffffff;
  background-image: url("../img/instagram.svg");
  background-repeat: no-repeat;
  background-position: center center;
}

.page-footer__join span {
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1.2px;
  display: block;
  text-transform: uppercase;
}

.page-footer__copyright {
  width: 260px;
  margin-left: auto;
  margin-right: auto;
  border-top: #3f3a41 1px solid;
  padding-bottom: 20px;
}

.page-footer__copyright p:nth-child(2) {
  position: relative;
}

.page-footer__copyright p:nth-child(2)::before {
  content: "";
  position: absolute;
  width: 15px;
  height: 20px;
  top: 3px;
  left: -20px;
  background-image: url("../img/lamp.png");
}

@media (min-width: @tablet-width) {
  .page-footer__wrapper {
    width: 600px;
    padding-top: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  .page-footer__main,
  .page-footer__catalog {
    display: flex;
    flex-direction: column;
  }

  .page-footer__contacts-item--callback::before {
    left: 2px;
  }

  .page-footer__contacts-item--mail::before {
    left: 0;
  }

  .page-footer__contacts-item--skype::before {
    left: 2px;
  }

  .page-footer__sign {
    display: flex;
    flex-direction: column;
    width: 320px;
    margin-left: auto;
    margin-right: auto;
  }

  .page-footer__sign-form {
    display: flex;
    flex-direction: column;
  }

  .page-footer__sign-form button {
    align-self: flex-end;
  }

  .page-footer__copyright {
    width: 600px;
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
  }
}

@media (min-width: @desktop-width) {
  .page-footer {
    text-align: left;
  }
  .page-footer__wrapper {
    width: 1302px;
    padding-top: 58px;
    padding-bottom: 28px;
    justify-content: flex-start;
  }

  .page-footer__main {
    width: 150px;
    margin-right: 182px;
  }

  .page-footer__main h3,
  .page-footer__catalog h3 {
    margin-bottom: 25px;
  }

  .page-footer__catalog {
    width: 155px;
    margin-right: 173px;
  }

  .page-footer__contacts {
    width: 225px;
    margin-right: 105px;
  }

  .page-footer__contacts h3 {
    font-size: 18px;
    letter-spacing: 1.7px;
    margin-top: 12px;
    margin-bottom: 0;
  }

  .page-footer__contacts-wrapper {
    width: 213px;
    justify-content: space-between;
    margin: 0;
    margin-top: 12px;
  }

  .page-footer__contacts-text {
    margin-top: 0;
    margin-bottom: 19px;
    letter-spacing: 0.4px;
  }

  .page-footer__contacts-item {
    font-size: 12px;
    line-height: 29px;
    letter-spacing: 1.2px;
  }

  .page-footer__contacts-item--callback,
  .page-footer__contacts-item--mail,
  .page-footer__contacts-item--skype {
    margin-left: 30px;
  }

  .page-footer__contacts-item--callback::before {
    left: -27px;
  }

  .page-footer__contacts-item--mail::before {
    left: -30px;
  }

  .page-footer__contacts-item--skype::before {
    left: -27px;
  }

  .page-footer__sign {
    margin: 0;
    width: 310px;
  }

  .page-footer__join {
    flex-direction: row;
    margin-top: 30px;
  }

  .page-footer__join span {
    margin-left: 23px;
  }

  .page-footer__copyright {
    width: 1302px;
    letter-spacing: 0.3px;
    box-sizing: border-box;
    padding-top: 26px;
    padding-left: 0;
    padding-right: 31px;
  }
  .page-footer__copyright p:nth-child(2)::before {
    left: -30px;
  }
}

