.slider {
  list-style: none;
  padding: 0;
  width: 92px;
  display: flex;
  justify-content: space-between;
}

.slider__item {
  position: relative;
}

.slider__item-toggle {
  visibility: hidden;
}

.slider__item-toggle + label .checkbox-indicator {
  position: absolute;
  top: 1px;
  left: 1px;
  width: 10px;
  height: 10px;
  box-sizing: border-box;
  border: #c1a067 2px solid;
  background-color: transparent;
}

.slider__item-toggle:checked + label .checkbox-indicator {
  background-color: #c1a067;
}
