.advertizing {
  margin-bottom: 50px;
}
.advertizing__wrapper {
  position: relative;
  width: 320px;
  margin-left: auto;
  margin-right: auto;
  min-height: 487px;
  background-image: url("../img/BG.png");
  background-repeat: no-repeat;
  background-position: top 0 right -500px;
}

.advertizing__wrapper::before {
  content: "";
  position: absolute;
  width: 110px;
  height: 278px;
  top: 170px;
  right: 10px;
  background-image: url("../img/perfum.png");
}

.advertizing__wrapper::after {
  content: "от 4 990 Р. —";
  position: absolute;
  width: 110px;
  height: 15px;
  top: 455px;
  right: 0;
  font-size: 13px;
}

.advertizing__name {
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
  width: 260px;
  position: absolute;
  top: 300px;
  left: 10px;
  margin-left: auto;
  margin-right: auto;
}

.advertizing__detail {
  display: none;
  position: absolute;
  top: 330px;
  left: 0;
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  color: #c1a067;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  text-decoration: none;
}

.advertizing__recomendation {
  display: none;
  position: absolute;
  top: 0;
  right: -18px;
  width: 330px;
  height: 260px;
  box-sizing: border-box;
  padding-left: 45px;
  padding-top: 3px;
  background-color: #f9f9f9;
}

.advertizing__recomendation h3 {
  font-size: 17px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  margin-bottom: 70px;
}

.advertizing__recomendation::before {
  content: "";
  position: absolute;
  top: 72px;
  left: 46px;
  width: 283px;
  height: 2px;
  background-color: #c1a067;
}

.advertizing__recomendation a {
  display: block;
  font-size: 16px;
  line-height: 36px;
  text-decoration: none;
  color: #302a31;
  letter-spacing: 0.5px;

  &:hover {
    color: #c16774;
  }
}

.advertizing__slider {
  display: none;
}

.advertizing__info {
  list-style: none;
  padding: 0;
  font-size: 14px;
  text-transform: uppercase;
  text-align: center;
  width: 260px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
}

.advertizing__info-item {
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.8px;
  margin-bottom: 30px;
  padding-bottom: 10px;
  border-bottom: #eceaed 1px solid;
}

@media (min-width: @tablet-width) {
  .advertizing__wrapper {
    width: 660px;
    background-position: top 0 right -300px;
  }

  .advertizing__name {
    font-size: 30px;
    top: 340px;
    left: 30px;
    width: 400px;
  }

  .advertizing__wrapper::before {
    top: 80px;
    right: 130px;
  }

  .advertizing__wrapper::after {
    top: 305px;
    right: 0;
  }

  .advertizing__slider {
    position: absolute;
    bottom: 0;
    left: 30px;
    display: flex;
  }

  .advertizing__info {
    width: 500px;
  }
}

@media (min-width: @desktop-width) {
  .advertizing {
    margin-top: 62px;
    margin-bottom: 28px;
  }

  .advertizing__wrapper {
    width: 1306px;
    background-position: top 0 right 3px;
  }

  .advertizing__name {
    font-size: 42px;
    font-weight: 800;
    line-height: 65px;
    letter-spacing: 2.5px;
    top: 103px;
    left: 2px;
    width: 550px;
  }

  .advertizing__wrapper::before {
    top: 100px;
    right: 490px;
  }

  .advertizing__wrapper::after {
    top: 335px;
    right: 330px;
  }

  .advertizing__detail {
    display: block;
  }

  .advertizing__detail::before {
    content: "";
    position: absolute;
    top: 33px;
    left: 2px;
    width: 222px;
    height: 2px;
    background-color: #c1a067;
  }

  .advertizing__recomendation {
    display: block;
  }

  .advertizing__slider {
    position: absolute;
    bottom: 23px;
    left: 274px;
    display: flex;
  }

  .advertizing__info {
    display: flex;
    justify-content: space-between;
    width: 1248px;
    margin-top: 63px;
  }

  .advertizing__info-item {
    border: none;
    text-align: left;
    width: 172px;
  }

  .advertizing__info-item:nth-child(1) {
    margin-left: 83px;
  }
}
