.arrow {
  display: none;
  width: 19px;
  height: 32px;
  box-sizing: border-box;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.arrow--left {
  position: absolute;
  top: 315px;
  left: 10px;
  background-image: url("../img/arrow-left.png");
}

.arrow--right {
  position: absolute;
  top: 315px;
  right: 10px;
  background-image: url("../img/arrow-right.png");
}

@media (min-width: @desktop-width) {
  .arrow {
    display: block;
  }
}
