.product-card {
  list-style: none;
  padding: 0;
  margin-top: 59px;
}

.product-card a {
  text-decoration: none;
  color: #302a31;
}

.product-card__item {
  width: 260px;
  min-height: 453px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
}

.product-card__item-wrapper {
  height: 260px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}


.product-card__name {
  font-size: 16.5px;
  line-height: 20px;
  text-transform: uppercase;
  max-width: 210px;
  margin: 29px auto 18px auto;

  &:hover {
    color: #c1a067;
  }

  &:active {
    opacity: 0.4;
  }
}

.product-card__brand,
.product-card__desc,
.product-card__sex {
  font-size: 13px;
  line-height: 21px;
  margin: 0;
}

.product-card__price {
  margin-top: 17px;
  font-size: 13px;
  line-height: 24px;
}

.product-card__price span {
  color: #c16774;
  text-decoration: line-through;
}

.product-card__item--sale {
  position: relative;
}

.product-card__item--sale::before {
  content: "SALE";
  font-size: 12px;
  padding-top: 5px;
  color: #ffffff;
  letter-spacing: 1px;
  position: absolute;
  width: 47px;
  height: 22px;
  top: 10px;
  left: 0;
  box-sizing: border-box;
  background-color: #c16774;
}

@media (min-width: @tablet-width) {
  .product-card {
    display: flex;
    flex-wrap: wrap;
    width: 640px;
    margin-left: auto;
    margin-right: auto;
  }

  .product-card__item--special .product-card__name {
    margin-top: 36px;
  }
}

@media (min-width: @desktop-width) {
  .product-card {
    width: 1360px;
    box-sizing: border-box;
    padding-left: 18px;
    padding-right: 20px;
  }

  .product-card__item--sale::before {
    top: 10px;
    left: -23px;
  }

  .product-card__item {
    position: relative;
  }

  .product-card__item:not(:first-of-type)::after {
    content: "";
    position: absolute;
    top: 15px;
    left: -35px;
    width: 1px;
    height: 412px;
    background-color: #eceaed;
  }
}

