.news__wrapper {
  margin-left: auto;
  margin-right: auto;
  width: 260px;
  padding-bottom: 20px;
}

.news h2 {
  margin-bottom: 45px;
}

.news__left,
.news__right {
  width: 260px;
  margin-bottom: 30px;
}

.news__left-item {
  margin-bottom: 32px;
}

.news__right-item {
  margin-bottom: 13px;
}

.news__left-item {
  padding-bottom: 25px;
  border-bottom: #eceaed 1px solid;
}

.news__name {
  margin-top: 22px;
  margin-bottom: 12px;
}

.news__name a {
  font-size: 16px;
  line-height: 18px;
  color: #302a31;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.4px;

  &:hover {
    color: #c16774;
  }
}

.news__date {
  font-size: 13px;
  line-height: 24px;
  color: #c1a067;
  margin: 0;
  margin-bottom: 10px;
}

.news__content {
  font-size: 15px;
  line-height: 21px;
  margin: 0;
}

.news__button {
  display: block;
  font-size: 10px;
  font-weight: 700;
  color: #c1a067;
  line-height: 24px;
  letter-spacing: 2.5px;
  text-transform: uppercase;
  background-color: transparent;
  border: none;
  width: 200px;
  height: 18px;
  margin-left: auto;
  margin-right: auto;
}

@media (min-width: @tablet-width) {
  .news__wrapper {
    width: 580px;
    padding-bottom: 40px;
  }

  .news__left,
  .news__right {
    width: 580px;
  }

  .news__right {
    margin-top: 60px;
  }

  .news__right-item {
    min-height: 185px;
    position: relative;
  }

  .news__right-item--renegades .news__right-wrapper,
  .news__right-item--korloff .news__right-wrapper,
  .news__right-item--amouage .news__right-wrapper {
    position: relative;
    z-index: 1;
  }

  .news__right-item--renegades::before {
    content: "";
    position: absolute;
    top: -20px;
    left: 0;
    width: 252px;
    height: 185px;
    background-image: url("../img/renegade.png");
    z-index: 0;
  }

  .news__right-item--korloff::before {
    content: "";
    position: absolute;
    top: -20px;
    right: -22px;
    width: 252px;
    height: 185px;
    background-image: url("../img/korloff.png");
    z-index: 0;
  }

  .news__right-item--amouage::before {
    content: "";
    position: absolute;
    top: -20px;
    left: 0;
    width: 252px;
    height: 185px;
    background-image: url("../img/amouage.png");
    z-index: 0;
  }

  .news__right-item--renegades .news__right-wrapper,
  .news__right-item--amouage .news__right-wrapper {
    width: 400px;
    margin-left: 185px;
  }

  .news__right-item--korloff .news__right-wrapper {
    width: 350px;
  }
}

@media (min-width: @desktop-width) {
  .news__wrapper {
    width: 1300px;
    display: flex;
    padding-left: 26px;
    padding-right: 26px;
    padding-bottom: 95px;
  };

  .news__left,
  .news__right {
    margin: 0;
  }

  .news__left {
    margin-right: 97px;
  }

  .news__right-item--renegades .news__right-wrapper,
  .news__right-item--amouage .news__right-wrapper {
    width: 420px;
    margin-left: 202px;
  }

  .news__right-item--renegades .news__name {
    margin-top: 19px;
  }

  .news__right-item--renegades .news__content {
    margin-top: 15px;
  }

  .news__right-item--korloff .news__right-wrapper {
    width: 400px;
  }

  .news__left .news__button {
    margin-left: 205px;
    margin-top: -7px;
  }

  .news__right .news__button {
    margin-right: 175px;
    margin-top: -10px;
  }
}

