.brands {
  margin-bottom: 40px;
}

.brands h2 {
  margin-bottom: 21px;
}

.brands__desc {
  font-size: 10px;
  font-weight: 700;
  letter-spacing: 2px;
  line-height: 24px;
  color: #c1a067;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.brands__logo {
  text-align: center;
  width: 260px;
  margin-left: auto;
  margin-right: auto;
}

.brands__list {
  list-style: none;
  padding: 0;
  width: 260px;
  box-sizing: border-box;
  margin-left: auto;
  margin-right: auto;
}

.brands__item {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin-bottom: 30px;
}

.brands__item a {
  font-size: 16px;
  line-height: 36px;
  color: #302a31;
  text-decoration: none;

  &:hover {
    color: #c1a067;
  }
}

@media (min-width: @tablet-width) {
  .brands {
    margin-bottom: 60px;
  }

  .brands__list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 640px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .brands__item {
    text-align: center;
    width: 200px;
  }
}

@media (min-width: @desktop-width) {
  .brands {
    margin-bottom: 90px;
  }

  .brands__logo {
    width: 1307px;
    margin-bottom: 33px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .brands__list {
    width: 1100px;
  }

  .brands__item {
    text-align: left;
    width: 200px;
  }
}
