body {
  font-family: "Circe", "Arial", sans-serif;
  font-weight: 400;
  color: #302a31;
  min-width: 320px;
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Circe";
  src: url("../fonts/circe-regular.woff2"), url("../fonts/circe-regular.woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Circe";
  src: url("../fonts/circe-bold.woff2"), url("../fonts/circe-bold.woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Circe";
  src: url("../fonts/Circe-ExtraBold.woff2"), url("../fonts/Circe-ExtraBold.woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
.page-header__main,
.page-header__user {
  list-style: none;
  padding: 0;
  width: 100%;
}
.page-header__user {
  margin-top: -15.5px;
}
.page-header__menu-wrapper {
  display: flex;
  height: 50px;
  justify-content: flex-end;
  background-color: #c1a067;
  margin-bottom: -15px;
}
.page-header__menu--closed {
  display: none;
}
.page-header__menu-button {
  background-color: transparent;
  border: none;
  width: 70px;
  margin-right: 10px;
  cursor: pointer;
}
.page-header__menu a {
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;
}
.page-header__menu a:hover {
  color: #302a31;
}
.page-header__menu a:active {
  opacity: 0.4;
}
.page-header__main-item,
.page-header__user-item {
  box-sizing: border-box;
  padding-top: 15px;
  background-color: #c1a067;
  height: 50px;
  text-align: center;
  border-bottom: #ffffff 1px solid;
}
.page-header__info {
  width: 260px;
