  margin-right: auto;
}
.page-header__info img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}
.page-header__info-wrapper {
  width: 260px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.page-header__search {
  position: relative;
}
.page-header__search input {
  position: relative;
  width: 260px;
  height: 45px;
  margin-top: 40px;
  box-sizing: border-box;
  padding-bottom: 10px;
  border: none;
  border-bottom: #c1a067 2px solid;
  outline: none;
}
.page-header__search input:focus {
  border-bottom: #c16774 2px solid;
}
.page-header__search::before {
  position: absolute;
  content: "";
  top: 50px;
  right: 2px;
  width: 14px;
  height: 14px;
  background-image: url("../img/search.png");
  z-index: 3;
}
.page-header__search input::placeholder {
  font-size: 12px;
  line-height: 24px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.page-header__contacts {
  list-style: none;
  padding: 0;
  margin-top: 30px;
  margin-left: 20px;
}
.page-header__contacts-item {
  position: relative;
  margin-bottom: 25px;
}
.page-header__contacts-item a {
  font-size: 12px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  text-decoration: none;
  color: #302a31;
}
.page-header__contacts-item a:hover {
  color: #c1a067;
}
.page-header__contacts-item--callback::before {
  content: "";
  position: absolute;
  width: 9px;
  height: 11px;
  top: 5px;
  left: -20px;
  background-image: url("../img/tel.png");
}
.page-header__contacts-item--mail::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 11px;
  top: 5px;
  left: -20px;
  background-image: url("../img/mail.png");
}
.page-header__contacts-item--time::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  top: 5px;
  left: -20px;
  background-image: url("../img/time.png");
}
.page-header__cart {
  display: none;
  position: relative;
  width: 90px;
  height: 84px;
  background-color: #c1a067;
  margin-top: 17px;
  margin-left: 110px;
}
.page-header__cart::before {
  position: absolute;
  content: "";
  width: 32px;
  height: 41px;
  top: 20px;
  left: 27px;
  background-image: url("../img/cart.png");
}
.page-header__cart::after {
  position: absolute;
  content: "4";
  color: #ffffff;
  font-size: 10px;
  font-weight: 700;
  width: 10px;
  height: 10px;
  top: 10px;
  right: 5px;
}
.page-header__catalog {
  background-color: #302a31;
  width: 100%;
  margin-bottom: 30px;
}
.page-header__catalog-button {
  width: 100%;
  height: 50px;
  margin-bottom: 0;
  box-sizing: border-box;
  border: #302a31 2px solid;
  color: #302a31;
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  background-color: #ffffff;
  cursor: pointer;
}
.page-header__catalog-list {
  list-style: none;
  padding: 0;
  width: 100%;
  margin: 0 auto;
}
.page-header__catalog-list--closed {
  display: none;
}
.page-header__catalog-item {
  height: 50px;
