}
.page-header__catalog-item a {
  font-size: 20px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: middle;
}
.page-header__catalog-item a:hover {
  color: #c16774;
}
.page-header__catalog-item a:active {
  opacity: 0.4;
}
