.page-main h2 {
  font-size: 20px;
  font-weight: 700;
  letter-spacing: 3.5px;
  text-transform: uppercase;
  text-align: center;

  width: 300px;
  padding-bottom: 18px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  margin-top: 20px;
  border-bottom: #c1a067 2px solid;
}


@media (min-width: @tablet-width) {
  .page-main h2 {
    font-size: 25px;
    font-weight: 700;
    width: 420px;
    margin-bottom: 50px;
  }
}

@media (min-width: @desktop-width) {
  .page-main h2 {
    font-size: 27px;
    line-height: 34px;
  }
}
